main {
	margin: 0 auto;
	max-width: 90%;
  padding: 50px 30px;
  
} 

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  background-position: center;
  background-repeat: no-repeat;
  margin: auto;
  padding: 0;
  font-weight: 600;
  line-height: 1.25;
  font-size: 12px;
  letter-spacing: .1em;
  font-family: 'arial', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  margin: auto;
  line-height: 1.25;
}

